import hxNumberBox from '@/components/uni-number-box/uni-number-box.vue';
export default {
	components: {
		hxNumberBox,
	},
	data() {
		return {
			//购物车商品价格合计
			goodsTotalPrice: 0,
			//购物车商品数量合计
			goodsTotalNumber: 0,
			cartAnimationData: {},
			element: [],
			//foot 高度
			footHeight: '0',
			num: 1,
			//显示购物车
			showCar: false,
			//购物车中商品滚动条位置
			carGoodsScrollTop: 0,
			//购物车缓存 Storage 名称
			shoppingCartStorageName: 'shopping_cart',
			sizeCalcState: false,
			tabScrollTop: 0,
			mainHeight: 0,
			currentId: 0,
			showFoot:true,
			//购物车显示折扣
			showDiscount: true
		}
	},
	props:{
		storeData:{
			type: Object,
			default: function(){
				return {};
			}
		},
		shoppCart:{
			type: Object,
			default: function(){
				return {};
			}
		},
		ccsl:{
			type:String,
			default:function(){
				return '';
			}
		}
	},
	created() {
		let _this = this;
		console.log('这里',_this.storeData,_this.shoppCart)
	},
	methods: {
		//小球跳跃动画
		touchOnAddGoods(ele, data) {
			console.log('23e',ele, data)
			this.element = [ele, '.cart'];
			this.num++;
		},
		//新增商品计算价格
		addGoodsChange(number, rowData) {
			console.log('5675',rowData)
			number = Number(number);
			var that = this;
			let totalPrice = 0;
			let totalNumber = 0;
			let existedGoods = false;
			let goods_id = rowData.goods_id
			that.iGlobal.request({
				url:'/diandi_shop/cart/add',
				data:{
					goods_id:goods_id,
					num:number,
					spec_id:''
				},
				method:'POST'
			}).then((res)=>{
				console.log('加入购物车状态',res.code)
				if(res.code=='200'){
					that.$emit("cartChange", res.data);
					
				}else{
					that.iGlobal.showToast(res.message);
				}
			}).catch((err)=>{
				console.log("错误",err);
			});
			//购物车商品数据缓存至本地
			// uni.setStorageSync(that.shoppingCartStorageName, that.shoppingCartAll);
		},
		//显示购物车
		showShoppingCar() {
			if (this.shoppCart.goodsTotalNumber == 0) {
				return;
			}
			this.footHeight = '100%';
			this.showCar = true;
			this.carGoodsScrollTop = 0;
		},
		//隐藏购物车
		hideShoppingCar() {
			this.footHeight = '0';
			this.showCar = false;
		},
		//清空该门店的购物车
		clearShoppingCart() {
			let that = this;
			that.iGlobal.request({
				url:'/diandi_shop/cart/clear',
				data:{},
				method:'POST'
			}).then((res)=>{
				console.log('清空购物车状态',res.code)
				if(res.code =='200'){
					that.$emit("clearCart",res.data);
					
				}else{
					that.iGlobal.showToast(res.message);
				}
			}).catch((err)=>{
				console.log("错误",err);
			});
			
			that.hideShoppingCar();
		},
		mpClear(e) {
			// TODO nvue 取消冒泡
			e.stopPropagation();
		},
		//计算每类商品购买的总数
		setLabelNumber() {
			let that = this;
			//计算每一类购买商品的总数量
			for (let j in that.categoryList) {
				let n = 0;
				for (var i in that.shoppCart) {
					if (that.shoppCart[i].category_id == that.categoryList[j].category_id) {
						n += that.shoppCart[i].number;
					}
				}
				that.categoryList[j].number = n;
			}
		},
		
		//去结算
		jiesuan() {
			uni.navigateTo({
				url:'../order/confirm'
			})
		},
		//联系商家
		contact(e) {
			let phone =  this.storeData.mobile
			console.log(phone)
			if (!phone) {
				return;
			}
			uni.makePhoneCall({
				phoneNumber: phone //仅为示例
			});
		},
		
	}
}
